.App {
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  /* temporary */
  background-color: #222;
}

.bg-img {
  position: relative;
  width: 100%;
  height: 100%;
  background: url("https://imgur.com/ltiAppv.jpg") center center no-repeat;
  background-size: cover;
  z-index: 1;
}
.bg-img:after {
  content: "";
  z-index: -1;
  position: absolute;

  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-image: radial-gradient(rgba(0, 0, 0, 0.2), #000);
  opacity: 0.7;
  /* -webkit-animation: colorChange 20s linear 0s infinite;
  animation: colorChange 20s linear 0s infinite; */
}

/* footer */
.footer {
  position: fixed;
  max-height: 50px;
  width: 100%;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  flex-direction: row;
  padding-right: 20px;
  /* background-color: #111; */
}
.qr-image {
  width: 120px;
  height: 160px;
}

.footer-text {
  color: white;
}

/* //typed css */
/* Adding CSS manually is only necessary in JSFiddle,
   where CSS can't be inserted into the <head> */
.typed-cursor {
  opacity: 1;
  animation: typedjsBlink 0.7s infinite;
  -webkit-animation: typedjsBlink 0.7s infinite;
  animation: typedjsBlink 0.7s infinite;
}
@keyframes typedjsBlink {
  50% {
    opacity: 0;
  }
}
@-webkit-keyframes typedjsBlink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.typed-fade-out {
  opacity: 0;
  transition: opacity 0.25s;
  -webkit-animation: 0;
  animation: 0;
}

.type-wrap {
  color: #fff;
  font-size: 50px;
  margin-top: 15px;
  word-wrap: break-word;
  font-family: "Special Elite", cursive;
}

@media (max-width: 767px) {
  .type-wrap {
    color: #fff;
    font-size: 2.2em;
    font-family: "Special Elite", cursive;
  }
}

/* //side div */
