button {
  color: white;
  margin-top: 25px;
  font-size: 18px;
  background-color: #333;

  padding-top: 10px;
  padding-bottom: 10px;
  /* padding: 0.2em 1em; */
  width: 12rem;
  font-weight: bold;
  border: 1.5px solid white;
  outline: none;
  border-radius: 35px;
}

@media (max-width: 767px) {
  button {
    margin-top: 15px;
    /* margin-left: -5px; */
    align-self: center;
  }
}

button:hover {
  background-color: white;
  color: gray;
  cursor: pointer;
  border-radius: 35px;
}

.padding {
