}

/* --- disqus css -- */
.textarea-wrapper {
  color: orange !important;
}

/* //for model */
.modal_open {
  cursor: pointer;
  color: #fff;
  font-weight: bold;
  font-size: 22px;
}

.modal_open:hover {
  color: #333;
}

.modal {
  outline: none;
  background: white;
  font-size: 1.6rem;
  width: 50%;
  max-width: 80%;

  z-index: 15;
  max-height: 80%;
  border-radius: 10px;

  will-change: transform;
  transform: translate3d(0, 0, 0);
}

@media (max-width: 767px) {
  .modal {
    width: 100%;
    max-width: 100%;
    margin-bottom: 0;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
  }
}

.model_container {
  display: flex;
  flex-direction: row;
  align-items: center;
  text-align: center;
  justify-content: center;
}

.marker_image {
  max-width: 60%;
  max-height: 60%;
}

/* //welcome text */

